/* App.css */
.App {
  text-align: center;
}

.pdf-container {
  width: 80%;
  margin: 20px auto;
  border: 1px solid #ddd;
  padding: 20px;
  overflow: hidden; /* 防止 PDF 显示时超出容器 */
}

.pdf-document {
  width: 100%;
}

button {
  margin: 10px;
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
}

input[type="file"] {
  margin: 20px;
}
